import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Button } from "../Button";
import { Input } from "../Input";
import { RadioButtons } from "../RadioButtons";
import { Toggle } from "../Toggle";
import styles from "./form.module.scss";
import eye from "../../images/eye.png";
import eye1 from "../../images/eye1.png";
export const RegistrationForm = () => {
  const [passwordType, setPasswordType] = useState("password");

  const [users, setUsers] = useState([]);
  const [create, setCreate] = useState(false);
  useEffect(() => {
    fetch("http://localhost:5000/api/users")
      .then(function (response) {
        return response.json();
      })
      .then(function (data) {
        setUsers(data);
      });
  }, [create]);

  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const handleEmailChange = (event) => {
    setEmail(event.target.value);
  };
  const [password, setPassword] = useState("");
  const handlePasswordChange = (event) => {
    setPassword(event.target.value);
  };
  const [name, setName] = useState("");
  const handleNameChange = (event) => {
    setName(event.target.value);
  };
  const [surname, setSurname] = useState("");
  const handleSurnameChange = (event) => {
    setSurname(event.target.value);
  };
  const [date, setDate] = useState("");
  const handleDateChange = (event) => {
    setDate(event.target.value);
  };
  const [sex, setSex] = useState("");
  const handleSexChange = (event) => {
    console.log(sex);
    if (event.target.id === "radio-4") {
      setSex("male");
    } else if (event.target.id === "radio-3") {
      setSex("female");
    }
  };
  const handleFormSubmit = (event) => {
    event.preventDefault();

    const validCount = 3;
    if (name.length < validCount) {
      alert(`      ${validCount} `);
    } else if (surname.length < 3) {
      alert("      3 ");
    } else if (email.length < 8) {
      alert(" email     8 ");
    } else if (password.length < 8) {
      alert("     8 ");
    } else if (!email.match(/^[^\s@]+@[^\s@]+\.[^\s@]+$/)) {
      alert("  email");
    } else if (users.find((user) => user.email === email)) {
      alert("user   email  ");
    } else {
      fetch("http://localhost:5000/api/registration", {
        method: "post",
        body: JSON.stringify({ email, password, name, surname }),
        headers: {
          "content-type": "application/json",
        },
      })
        .then(function (response) {
          return response.json();
        })
        .then(function (data) {
          localStorage.setItem("users", JSON.stringify(data));
        });
      setCreate(true);
      navigate("/");
    }
  };
  const handleIconClick = () => {
    if (passwordType === "text") {
      setPasswordType("password");
    } else if (passwordType === "password") {
      setPasswordType("text");
    }
  };
  const handleClick = () => {
    navigate("/signin");
  };
  return (
    <form onSubmit={handleFormSubmit} className={styles.form}>
      <div className={styles.reg}> </div>
      <div className={styles.input}>
        <Input
          value={name}
          onChange={handleNameChange}
          name="name"
          placeholder=""
          color="#BC9CFF"
        />
        <Input
          value={surname}
          onChange={handleSurnameChange}
          name="surname"
          placeholder=""
          color="#BC9CFF"
        />
      </div>
      <div className={styles.radio}>
        <RadioButtons
          onChange={handleSexChange}
          name="male"
          id="radio-3"
          text=""
        />
        <RadioButtons
          onChange={handleSexChange}
          name="male"
          id="radio-4"
          text=""
        />
      </div>

      <h4 className={styles.label}>  </h4>
      <Input
        value={date}
        onChange={handleDateChange}
        name="date"
        type="date"
        placeholder=".."
        color="#BC9CFF"
      />

      <div className={styles.enter}>    </div>
      <div className={styles.input1}>
        <Input
          placeholder="Email"
          value={email}
          onChange={handleEmailChange}
          name="email"
          color="#BC9CFF"
        />
        <div className={styles.password}>
          <Input
            placeholder=""
            value={password}
            onChange={handlePasswordChange}
            name="password"
            color="#BC9CFF"
            type={passwordType}
          />
          {passwordType === "password" ? (
            <img
              className={styles.icon}
              width={30}
              onClick={handleIconClick}
              src={eye1}
            />
          ) : (
            <img
              className={styles.icon}
              width={30}
              onClick={handleIconClick}
              src={eye}
            />
          )}
        </div>
      </div>
      <div className={styles.toggle}>
        <Toggle text=" " />
      </div>
      <div className={styles.button1}>
        <Button
          text=""
          bigButton={true}
          isIcons={true}
          color="white"
          type="submit"
        />
      </div>
      <div className={styles.title}>
        <div className={styles.toxin}>    Toxin</div>
        <div className={styles.button3}>
          <Button text="" onClick={handleClick} withFrame={true} />
        </div>
      </div>
    </form>
  );
};
